.home-view-wrapper {
  .links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .link-card {
      width: 200px;
      height: 200px;
      margin: 10px;
      border-radius: 10px;
      position: relative;
      cursor: pointer;
      p {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

.playlists-view-wrapper {
  .search-playlists-input-wrapper {
    display: inline-flex;
    align-items: center;
    margin-bottom: 20px;
  }
  .search-input {
    width: 140px;
  }
  .playlists-wrapper {
    width: 100%;
    display: block;

    .playlist-wrapper {
      height: 250px;
      width: 200px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 10px;
      margin-bottom: 10px;
      padding: 10px;
      display: flex;
      flex-direction: column;
      border-radius: 5px;
      cursor: pointer;

      .cover-image {
        width: 200px;
        height: 200px;
      }

      p {
        padding: 5px;
      }
    }
  }
}

.user-selection-wrapper {
  .form-wrapper {
    max-width: 400px;

    .create-playlist-form {
      max-width: 400px;
      margin: 5px;

      display: flex;
      flex-direction: column;
      align-items: left;

      .playlist-inputs {
        display: flex;
        flex-direction: column;
        margin: 2.5px;

        input {
          max-width: 150px;
        }
        .playlist-description {
          max-width: 100%;
        }
      }
      .export-button-wrapper {
        display: flex;
        justify-content: flex-end;
        margin-top: 15px;

        .export-button {
          max-width: 120px;
        }
      }
    }
  }
}

.table-section-wrapper {
  overflow-x: scroll;
  .table-wrapper {
    position: relative;
    margin: auto;
    padding: 5px;
    max-width: 1000px;

    .table-controls-wrapper {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 5px;
      .key-and-mode-filter-wrapper form {
        display: inline-flex;

        .filter-wrapper {
          margin: 5px;
          margin-right: 10px;
          display: inline-flex;
          align-items: center;
          select {
            margin-left: 5px;
          }

          button {
            margin-left: 5px;
            padding: 0;
            cursor: pointer;
          }
        }
      }

      .search-input-wrapper {
        display: flex;
        align-items: center;
        margin: 5px;
        input {
          width: 90px;
        }
        button {
          margin-left: 5px;
          padding: 0;
          cursor: pointer;
        }
      }
    }
    table {
      position: relative;
      width: 100%;
      margin: 0px;

      th {
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
      }
    }
  }
}

.table-ajax-cover {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: 20;
  background: rgba(0, 0, 0, 0.05);
  .ajax-spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.disabled-icon {
  opacity: 0.2;
}

.spinner-wrapper {
  visibility: hidden;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 200;

  .spinner-content {
    visibility: visible;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 10px;
    transform: translate(-50%, -50%);
    background-color: lightgray;
    border-radius: 5px;
    p {
      border-radius: 2px;
    }
  }
}

.spinner,
.spinner:after {
  border-radius: 50%;
  width: 3em;
  height: 3em;
}
.spinner {
  margin: 10px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 0.7em solid darkgrey;
  border-right: 0.7em solid darkgrey;
  border-bottom: 0.7em solid darkgrey;
  border-left: 0.7em solid lightgray;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 0.8s infinite linear;
  animation: load8 0.8s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.flash-messages-wrapper {
  visibility: hidden;
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 300;
  .flash-message-content {
    visibility: visible;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
  }
}

/* Larger than phone */
@media (min-width: 400px) {
  .playlists-view-wrapper {
    .playlists-wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;

      .playlist-wrapper {
        margin: 5px;
      }
    }
  }
}

/* Larger than phablet */
@media (min-width: 550px) {
}

.selection-button-icon {
  cursor: pointer;
}
